const ChatList = ({ messages }) => {
  return (
    <ul>
      <style jsx>{`
        ul {
          height: 100%;
          width: 100%;
          display: flex;
          text-align: right;
          padding: 0;
          flex-direction: column-reverse;
        }
        li {
          font-size: 1.6rem;
          list-style: none;
          margin: 20px;
        }
        .Mark {
            color: orange;
        }
        .Chance {
            color: lightblue;
            text-align: left;
        }
      `}</style>
      {messages.reverse().map(msg => {
        return (
          <li className={msg.author} >
            <strong>{msg.author}</strong> : {msg.text}
          </li>
        );
      })}
    </ul>
  );
};

export default ChatList;
